import Vue from "vue"
import Vuex from "vuex"
import mutations from "./mutations"
import actions from "./actions"

Vue.use(Vuex);

const store=new Vuex.Store({
        state:{
            topiclist:[
                {name:"all",desc:""},
                {name:"good",desc:""},
                {name:"share",desc:""},
                {name:"ask",desc:""},
                {name:"job",desc:""},
                {name:"dev",desc:""}
              ],
              isShow:false,//actionsheet
              islogin:false,//
              loginend:{
                center:"",
                msgs:"",
                create:"",
                logout:""
              },
              unlogin:{
                login:"",
                reginst:'',
                menus3:","
              },
              loginname:"", //
              accesstoken:"",  //
              author_id:"" ,//id
              mesCount:""

        },
        getters:{
            moreMeuns(state){
                //
                if(state.islogin){ //
                  return state.loginend  //
                }
                return state.unlogin  //
              },
              //
              filelist(state){
                return function(tab){
                  for (let i = 0; i < state.topiclist.length; i++) {
                    const element = state.topiclist[i];
                    if(element.name==tab){
                      return state.topiclist[i].desc
                    }
                  }
                }
              }
        },
        mutations,
        actions
})


export default store;



