
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import {mavonEditor} from "mavon-editor"
import "mavon-editor/dist/css/index.css"
import axios from "axios";
export default {
components:{
XTextarea,
Group,
XButton,
mavonEditor
},
data(){
return{
title:"", //
content:"", //
toolbars: {
bold: true, // 
italic: true, // 
header: true, // 
underline: true, // 
strikethrough: true, // 
mark: true, // 
superscript: true, // 
subscript: true, // 
quote: true, // 
ol: true, // 
ul: true, // 
link: true, // 
imagelink: true, // 
code: true, // code
table: true, // 
fullscreen: true, // 
readmodel: true, // 
htmlcode: true, // html
help: true, // 
/* 1.3.5 */
undo: true, // 
redo: true, // 
trash: true, // 
save: true, // eventssave
/* 1.4.2 */
navigation: true, // 
/* 2.1.8 */
alignleft: true, // 
aligncenter: true, // 
alignright: true, // 
/* 2.2.1 */
subfield: true, // 
preview: true, // 
}
}
},
methods:{
handletopic(){
var  app=this;
this.$http.post("/topics",{
accesstoken:this.$store.state.accesstoken,
tab:"dev",
title:this.title,
content:this.content
}).then(function(res){
console.log(res.data)
if(res.data.success){

app.$router.push("topiclist/dev")
}
})
}
},
imgAdd(pos,file){
console.log(pos);
console.log(file);
var app=this;
var params=new FormData();
params.append("smfile",file)

axios.post("https://sm.ms/api/v2//upload",{
headers:{
'Content-Type' : 'multipart/form-data',
'Authorization':""                      //
}
}).then(function (res){
console.log(res.data)

app.$refs.imgUpload.$img2Url(pos,res.data.url)
})
}
}



